.about {
    background-color: #EBD9FF;
    width: 75vw;
    padding: 0 100px;
    font-size: 16px;
    overflow-y: hidden;
    position: relative;
    left: 4%;
}

.about p,
.about h2,
.about h3 {
    background-color: inherit;
}

.about h2 {
    text-align: center;
    font-size: 28px;
}